import React from 'react';
import { ActivityIndicator, Button, Text, View, TextInput } from 'react-native';

class HUDBar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      index:1,
    }
  }
  
  componentDidMount() {
  }

  render() {
    let address = <ActivityIndicator />;
    let tokenAmount = <ActivityIndicator />;
    let etherAmount = <ActivityIndicator />;
    
    let ts0 = {textAlign:'center'};
    let ts1 = {textAlign:'center'};
    let ts2 = {textAlign:'center'};
    const ss = {textAlign:'center',fontWeight:'bold',color:'white'};

    if(this.state.index == 0 ) ts0 = ss;
    else if(this.state.index == 1 ) ts1 = ss;
    else if(this.state.index == 2 ) ts2 = ss;

    return (
      <View style={{backgroundColor:'gainsboro',borderTopWidth:1,borderColor:'darkgray'}}>
        <View style={{padding:15,flexDirection:'row',justifyContent:'space-between'}}>
          <Text>WALLET ADDRESS</Text>
          <Text>{address}</Text>
        </View>
        <View style={{padding:15,flexDirection:'row',justifyContent:'space-between'}}>
          <Text>PBH TOKEN</Text>
          <Text>{tokenAmount}</Text>
        </View>
        <View style={{padding:15,flexDirection:'row',justifyContent:'space-between'}}>
          <Text>ETHER</Text>
          <Text>{etherAmount}</Text>
        </View>
        <View style={{flexDirection:'row',padding:5}}>
          <View style={{flex:1,backgroundColor:'gray',padding:5,paddingVertical:15}}>
            <Text style={ts0}>WALLET</Text>
          </View>
          <View style={{width:5}} />
          <View style={{flex:1,backgroundColor:'gray',padding:5,paddingVertical:15}}>
            <Text style={ts1}>DAPPS</Text>
          </View>
          <View style={{width:5}} />
          <View style={{flex:1,backgroundColor:'gray',padding:5,paddingVertical:15}}>
            <Text style={ts2}>INVEST</Text>
          </View>
        </View>
      </View>
    );
  }

}

export default HUDBar;


